import React from "react";
import styles from "./CarService.module.css";
import { Button } from "@mui/material";
import { cardService, IProblems } from "../../state/types";

interface IProps {
  nameMaster: string;
  id: number;
  VIN: string;
  region: string;
  country: string;
  manufacturer: string;
  vehicleAttributes: string;
  checkDigit: string;
  modelYear: string;
  assemblyPlant: string;
  serialNumber: string;
  problems: IProblems;
  togglePopup: any;
  funSetCurrentCar: any;
}

const CarsInService: React.FC<IProps> = ({
  nameMaster,
  id,
  VIN,
  region,
  country,
  manufacturer,
  vehicleAttributes,
  checkDigit,
  modelYear,
  assemblyPlant,
  serialNumber,
  problems,
  togglePopup,
  funSetCurrentCar,
}) => {
  const car = {
    nameMaster,
    id,
    VIN,
    region,
    country,
    manufacturer,
    vehicleAttributes,
    checkDigit,
    modelYear,
    assemblyPlant,
    serialNumber,
    problems,
    togglePopup,
    funSetCurrentCar,
  };
  const CurrentCar = (carCurr: cardService) => {
    funSetCurrentCar(carCurr);
    togglePopup(true);
  };
  
  return (
    <div>
      <div className={styles.carService}>
        <h5>
           {manufacturer}    {car.serialNumber}{" "}
             - {nameMaster}
        </h5>
        <div>
          <p> {modelYear}  </p>
          <p>   {assemblyPlant}</p>
          <p>   {vehicleAttributes}</p>
        </div>
        <div>
          <Button
            onClick={() => CurrentCar(car)}
            sx={{
              backgroundColor: "#382274",
              height: `30px`,
              width: `180px`,
              margin: `15px`,
              transition: "all .8s",
              "&:hover": {
                background: "#e68d1a",
              },
            }}
            variant="contained"
          >
            
          </Button>
        </div>
      </div>
       
    </div>
  );
};

export default CarsInService;
